import { Component } from "react";
import "isomorphic-unfetch";
import Layout from "../components/Layout";
import styled from "styled-components";
import NextSeo from "next-seo";

import { Row, Col } from "reactstrap";

const HomeDiv = styled.div`
  height: 85vh;

  h1 {
    color: red;
  }
`;

export default class Index extends Component {
  static async getInitialProps() {
    // eslint-disable-next-line no-undef
    const res = await fetch("https://jsonplaceholder.typicode.com/todos");
    const json = await res.json();

    return { todos: json };
  }

  render() {
    {
      ("newsapi api key 37b82fead6954eb48711defd794b1f68 ");
      console.log(this.props.todos.title);
    }
    const filteredResults = this.props.todos.filter(todo => {
      return todo.title.indexOf("qui") !== -1;
    });

    return (
      <Layout>
        <HomeDiv>
          <NextSeo
            config={{
              title: "Santa & the Mrs of West Texas",
              description: "Santa and Mrs Claus services for West Texas"
            }}
          />
          <Row>
            <Col>
              <p>
                {this.props.todos.map((todo, key) => (
                  <div key={key}>{todo.title}</div>
                ))}{" "}
              </p>
            </Col>
            <Col>
              <p>
                {filteredResults.map((todo, key) => {
                  return <div key={key}> {todo.title} </div>;
                })}
              </p>
            </Col>
          </Row>
        </HomeDiv>
      </Layout>
    );
  }
}
